/* GLOBAL */

body {
  font-family: "Proza Libre", sans-serif;
  overflow-x: hidden;
}

/* HEADER */
#principal header {
  position: relative;
  margin: 0;
  width: 100%;
  height: 100vh;
  color: #fff;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradientBG 12s ease infinite;
}

#sobre header {
  position: relative;
  margin: 0;
  min-height: 120vh;
  width: 100%;
  color: #fff;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradientBG 12s ease infinite;
}

#projectos-bd header {
  position: relative;
  margin: 0;
}

#projectos-bd header h6 {
  color: #fff;
}

@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

/* NAVBAR */

#principal header .navbar {
  visibility: hidden;
  opacity: 0;
  width: 100%;
  background: #000;
  z-index: 100;
  transition: 300ms all ease;
}

#sobre header .navbar,
#projectos-bd header .navbar {
  background: #000;
  width: 100%;
  z-index: 100;
}

header a.nav-link {
  color: rgba(255, 255, 255, 0.815) !important;
}

header a.nav-link:hover {
  color: #fa823d !important;
}

header a.active-page {
  color: #fa823d !important;
}

.disp {
  visibility: visible !important;
  opacity: 1 !important;
}

header .navbar img {
  margin-left: 20%;
  width: 60px;
  height: 60px;
}

header .navbar h6 {
  width: 20%;
  font-size: 0.8rem !important;
  margin-bottom: -3px !important;
}

header .navbar h6 span {
  font-size: 0.6rem;
}

header .navbar .navbar-nav {
  width: 100%;
  justify-content: flex-end !important;
  margin-right: 1rem;
}

header .navbar li {
  padding: 0 10px;
}

header .navbar-toggler {
  margin-left: auto;
}

header #navbarNav .dropdown-menu {
  background: #000;
}

header #navbarNav .dropdown-item {
  color: #fff;
}

header #navbarNav .dropdown-item:hover {
  color: #ff5d2c;
  background: none !important;
}

/* FIM NAVBAR */

/* LANDING */

header h4.lg-heading {
  position: absolute;
  top: 15%;
  left: 8%;
}

header h4 .text-secondary {
  color: #ee7752 !important;
}

header h2.sm-heading {
  font-size: 80px !important;
  position: absolute;
  top: 35%;
  text-transform: uppercase;
  font-weight: bolder;
  text-shadow: 1px 3px 0 #969696, 5px 6px 6px #aba8a8;
  transition: text-decoration 300ms ease-in-out;
}

header .icons {
  text-align: center;
  width: 40%;
  position: absolute;
  bottom: 15px;
  left: 30%;
}

header .icons i,
footer .icons i {
  padding-right: 3px;
}

header button#second-btn {
  padding: 8px 14px !important;
  font-size: 0.9rem !important;
  color: rgba(255, 255, 255, 0.787) !important;
  background: #fa823d !important;
  position: absolute;
  bottom: 16%;
  left: 50%;
  transform: translateX(-50%);
  width: 130px;
}

header button#second-btn:hover {
  color: #fff !important;
  background: #ff5d2c !important;
}

header .icons a {
  color: rgba(255, 255, 255, 0.548) !important;
}

header .icons a:hover {
  color: #23a6d5 !important;
}

/* FIM LANDING */

/* PORTFOLIO */

/* WEB DESIGN */

section#web-design {
  position: relative;
  background: #23a6d5;
  padding: 80px 0 120px 0;
}

section#web-design .container {
  max-width: 100%;
}

section#web-design #mockup {
  width: 80%;
  margin-left: 10%;
  border-radius: 10px;
}

section#web-design h1 {
  font-weight: bolder;
  color: #fff;
  font-size: 2.5rem !important;
  margin-bottom: 1rem !important;
  text-shadow: 3px 5px 2px #474747;
}

section#web-design ul {
  padding-left: 15px !important;
}

section#web-design li {
  display: inline-block;
  font-weight: bolder;
  cursor: pointer;
  font-size: 1.3rem !important;
  margin-bottom: 0.5rem;
  list-style-type: none;
  transition: 300ms ease;
}

section#web-design li span {
  padding-right: 0.5rem;
}

section#web-design li img {
  width: 1rem;
  height: 1rem;
}

section#web-design li:hover img {
  animation: thumbsShake 300ms ease 3;
}

section#web-design li:hover {
  color: #ff5d2c;
}

@keyframes thumbsShake {
  0% {
    transform: rotate(0);
  }

  25% {
    transform: rotate(-20deg);
  }

  50% {
    transform: rotate(-45deg);
  }

  75% {
    transform: rotate(-20deg);
  }

  100% {
    transform: rotate(0);
  }
}

section#web-design .card {
  padding: 5px !important;
  border: none !important;
  background: transparent !important;
}

section#web-design .card h3 {
  color: #fff;
  font-size: 0.9rem !important;
  margin-left: 10px;
}

section#web-design .card h3 img {
  width: 15px;
  height: 15px;
  transform: rotate(-135deg);
}

section#web-design::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100px;
  background: linear-gradient(-180deg, #080808, #1991bd);
}

section#web-design a {
  color: #000;
  font-size: 1.5rem;
  animation: arrowUp 1.5s ease infinite;
}

section#web-design a i {
  color: #000;
  font-size: 1.5rem;
}

/* PROJECTOS */

section#projectos {
  position: relative;
  padding: 0 0 120px 0;
  background: linear-gradient(-180deg, #1991bd, #095069);
}

section#projectos .container {
  max-width: 100% !important;

}

section#projectos h1 {
  font-weight: bolder;
  color: #fff;
  font-size: 2.5rem !important;
  margin-bottom: 1rem !important;
  text-shadow: 3px 5px 2px #474747;
}

section#projectos .projecto-link {
  position: relative;
}

section#projectos .projecto-link::before {
  content: "Ver projecto";
  visibility: hidden;
  opacity: 0;
  border-radius: 5px;
  color: #fff;
  box-shadow: 5px 5px 5px rgb(0, 0, 0);
  padding: 8px 12px;
  background: #ff5d2c;
  position: absolute;
  top: 100%;
  width: auto;
  left: 50%;
  transform: translateX(-50%);
  transition: all 300ms ease-in-out;
  z-index: 1000;
}

section#projectos .projecto-link:hover::before {
  visibility: visible;
  opacity: 1;
  top: 45%;
}

section#projectos img {
  width: 100%;
  padding: 0.5rem;
  min-height: 300px;
  max-height: 300px;
  border-radius: 5px;
  transition: 500ms ease-in-out;
}

section#projectos .projecto-link:hover img {
  transform: scale(1.03);
  filter: grayscale(100%);
}

section#projectos h4 {
  color: #fff;
}

/* FIM PROJECTOS */

/* FIM WEB DESIGN */

/* FOTOGRAFIA */

.navbar2 {
  visibility: hidden;
  opacity: 0;
  top: 0px;
  width: 100%;
  z-index: 100;
  background: #fff;
  transition: 300ms ease;
}

.navbar2 ul {
  padding: 0.8rem;
}

.navbar2 ul li {
  margin-left: 1rem;
  align-self: center;
}

.navbar2 ul li a.nav-link {
  font-size: 1.5rem;
  color: #000 !important;
}

.c-disp {
  color: #ff5d2c !important;
}

.navbar2 ul li a:hover {
  font-size: 1.5rem;
  color: #ff5d2c !important;
}

.navbar2 img {
  width: 50px;
  height: 50px;
  margin-left: 10px;
}

section#fotografia {
  position: relative;
  background: #1991bd;
  background-size: 400% 400%;
  animation: gradientBD 12s ease infinite;
  padding: 100px 40px;
  transition: all 500ms ease-in-out;
}

section#fotografia .container {
  min-height: 300px;
}

section#fotografia #photo-camera {
  display: block;
  margin-bottom: 10px;
  margin-top: 40px;
  height: 130px;
}

.shutterClick {
  animation: cameraFlash 0.3s 0.3s alternate-reverse 1;
}

@keyframes cameraFlash {
  from {
    box-shadow: inset 1px 1px 1px 0px rgba(255, 255, 255, 0.671);
  }

  to {
    box-shadow: inset 1px 1px 1px 100px rgba(255, 255, 255, 0.739);
  }
}

section#fotografia .cameraWrap {
  padding: 0px 20px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
  opacity: 1;
  transition: 500ms ease-out;
}

section#fotografia .cameraWrap::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.cameraWrap.shutterClick:before {
  content: "";
  animation: cameraFlash 0.3s 0.3s alternate-reverse 1;
}

.volta {
  animation: volta 500ms linear 1;
}

@keyframes volta {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(90deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(270deg);
  }

  100% {
    transform: translateY(0deg);
  }
}

section#fotografia h5 {
  margin-top: 2rem;
  color: #fff;
  text-shadow: 2px 2px 0px #363636, 5px 4px 0px rgba(0, 0, 0, 0.15);
}

section#fotografia button {
  display: block;
  border-radius: 5px;
  opacity: 1;
  background: #ff5d2c13 !important;
  color: rgba(255, 255, 255, 0.719);
  margin: 30px auto 0 auto;
  padding: 10px;
  font-weight: bold;
  max-height: 100%;
  box-shadow: 7px 7px 5px 0px rgba(50, 50, 50, 0.75);
  transition: all 500ms ease-out;
}

section#fotografia button:hover {
  background: #ff5d2c !important;
  color: #fff;
  box-shadow: 2px 2px 5px 0px rgba(50, 50, 50, 0.75);
}

@keyframes gradientBD {
  0% {
    background-position: 0% -50%;
  }

  50% {
    background-position: 100% -50%;
  }

  100% {
    background-position: 0% -50%;
  }
}

section#fotografia .row {
  visibility: hidden;
  max-height: 0;
  opacity: 0;
  margin-left: auto;
  width: 100%;
  justify-content: center;
  transition: all 500ms ease-in-out;
}

section#fotografia .row .col1,
section#fotografia .row .col2,
section#fotografia .row .col3 {
  visibility: hidden;
  max-height: 0;
  opacity: 0;
}

.row-disp {
  visibility: visible !important;
  max-height: 100% !important;
  opacity: 1 !important;
}

.esconder {
  visibility: hidden !important;
  max-height: 0 !important;
  opacity: 0 !important;
}

section#fotografia .row img {
  padding: 5px;
  display: block;
  width: 100%;
  transition: 700ms ease-in-out;
}

section#fotografia .row img:hover {
  transform: scale(1.03);
}

section#fotografia::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 300px;
  background: linear-gradient(-180deg, transparent, #fff, #fff);
}

/* FIM FOTOGRAFIA */

/* LOGOTIPOS */

section#logotipos {
  padding: 0 0 60px 0;
}

section#logotipos h1 {
  font-size: 3rem !important;
  font-weight: bolder;
  text-shadow: 2px 2px 0px #ffffff, 5px 4px 0px rgba(0, 0, 0, 0.15);
}

footer#footer {
  padding: 80px 0 40px 0;
}

footer img {
  cursor: pointer;
  width: 40px;
  height: 40px;
  animation: arrowUp 2s ease infinite;
  margin-bottom: 1rem;
}

@keyframes arrowUp {
  0% {
    transform: translateY(-5px);
  }

  50% {
    transform: translateY(5px);
  }

  100% {
    transform: translateY(-5px);
  }
}

footer#footer ul {
  padding: 0 !important;
  margin: 1rem;
}

footer#footer ul li {
  list-style-type: none;
  padding: 1rem;
}

footer#footer ul a:hover {
  text-decoration: none;
}

footer#footer ul li:hover {
  color: #ff5d2c !important;
}

footer .icons a:hover {
  color: #fa823d;
}

