  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}

section#conhecimentos {
  position: relative;
  color: #fff;
  padding: 6rem 0;
  background: url(../images/grayscale-photo-of-computer-laptop-near-white-notebook-and-169573.jpg) center/ cover fixed no-repeat;
  background-blend-mode: lighten;
}

section#conhecimentos #dot h1 {
  font-size: 4rem !important;
  position: absolute;
  top: 50px;
  left: 48%;
  /*  animation: doting 2s ease infinite; */
}

section#conhecimentos .container {
  max-width: 90%;
}

section#conhecimentos .sobre-col {
  width: 90%;
  padding: 50px 0;
  background: #a79e9911;
  border-radius: 20px;
  -webkit-box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000,
    9px 11px 11px -1px rgba(125, 123, 123, 0.52);
  box-shadow: 5px 5px 5px 0px #000000, inset 4px 4px 15px 0px #000000,
    9px 11px 11px -1px rgba(125, 123, 123, 0.52);
}

section#conhecimentos img {
  display: block;
  width: 50%;
  box-shadow: 7px 7px 5px 0px rgba(0, 0, 0, 0.75);

  border-radius: 20px;
  margin: 0 auto 40px auto;
}

section#conhecimentos h3 {
  cursor: pointer;
  border: 3px dashed #fff;
  width: 60%;
  margin: auto;
  padding: 0.5rem 0.8rem;
  transition: 100ms ease-in;
}

section#conhecimentos h3:hover {
  border: 3px dotted #fa823d;
  color: #fa823d;
}

section#conhecimentos ul {
  padding: 0 !important;
  margin-top: 2rem;
}

section#conhecimentos ul li {
  font-weight: bold;
  font-size: 0.8rem;
  background: rgba(255, 255, 255, 0.562);
  list-style: none;
  color: #201908;
  border: 1px solid rgb(22, 20, 20);
  border-left: 6px solid rgb(212, 127, 15);
  width: 50%;
  margin: auto;
  padding: 0.5rem 1rem;
  margin-top: 5px;
  box-shadow: 5px 5px 5px rgb(0, 0, 0);
}

