/* MEDIA QUERIES */

/* Landscape phones and down */
@media (max-width: 480px) {
  header h4.lg-heading {
    font-size: 1.2rem !important;
    padding-right: 1.2rem;
    position: absolute;
    top: 15%;
    left: 8%;
  }

  header h4 .text-secondary {
    color: #ee7752 !important;
  }

  header h2.sm-heading {
    font-size: 40px !important;
  }

  #navbarNav2 li {
    margin-left: 0.5rem;
  }

  #navbarNav2 img {
    margin-left: 0;
  }

  #navbarNav2 a {
    font-size: 0.9rem;
    padding-bottom: 0;
  }

  section#web-design h1 {
    font-size: 2rem !important;
  }

  section#projectos .container {
    max-width: 100%;
  }

  section#fotografia {
    padding: 100px 5px;
  }

  footer#footer .links a {
    font-size: 0.8rem;
  }

  footer#footer .links a li {
    padding: 0.5rem;
  }

  footer#footer {
    padding-top: 10px !important;
  }

  section#conhecimentos h3 {
    width: 70%;
  }

  section#conhecimentos img {
    width: 80%;
  }

  section#sobre .row {
    width: 100%;
    left: 25px;
  }

  section#sobre h1 {
    font-size: 1.8rem;
  }

  #projectos-bd .row ul li {
    font-size: 0.9rem;
  }

  #projectos-bd .row ul li span img {
    width: 12.5px;
    height: 12.5px;
    margin-right: 10px;
  }

  #projectos-bd #projecto-ctn #ctn-foto-1::after {
    width: 40%;
    height: auto;
    padding: 10px;
    font-size: 0.8rem;
    top: 10%;
    right: 10%;
  }

  #projectos-bd .img-projecto img {
    min-height: 60vh !important;
    max-height: 60vh !important;
  }

  #projectos-bd #projecto-ctn .container img {
    margin-bottom: 60px !important;
    margin-top: 0 !important;
    min-height: 50vh !important;
  }
}

/* Landscape phone to portrait tablet */
@media (min-width: 481px) and (max-width: 767px) {
  header h2.sm-heading {
    font-size: 55px !important;
  }

  section#sobre h1 {
    font-size: 2rem;
  }
}

/* Portrait tablet to landscape and desktop */
@media (min-width: 768px) and (max-width: 979px) {
  header h2.sm-heading {
    font-size: 65px !important;
  }
}

@media (max-width: 768px) {
  section#projectos .container {
    max-width: 90% !important;
  }

  section#logotipos .projecto-link img {
    display: block;
    margin: auto;
  }

  #projectos-bd #restaurante-projecto::after,
  #projectos-bd #carro-projecto::after,
  #projectos-bd #foto-projecto::after,
  #projectos-bd #bolos-projecto::after {
    display: none;
  }

  #projectos-bd #projecto-ctn h6 {
    display: none;
  }
}

@media (max-width: 992px) {
  section#web-design h1 {
    margin-top: 2rem;
    padding-left: 20px;
  }

  section#web-design {
    padding-bottom: 80px !important;
  }

  #projectos-bd .row ul {
    padding-top: 20px;
    padding-left: 20px;
  }

  #projectos-bd #projecto-ctn .container {
    max-width: 95%;
  }

  #projectos-bd #projecto-ctn .container img {
    margin-bottom: 20px;
    margin-top: 0 !important;
    min-height: 70vh;
  }
}

@media (min-width: 993px) {
  section#conhecimentos h3 {
    width: 50%;
  }
}

